import React, { ChangeEvent, useState } from 'react';

// Hooks
import { useGlobalData } from '../../../../hooks/useGlobalData';

// Static
import staticData from '../../../../miscellaneous/history/calendarPage.json';

// Styles
import {
    InputWrapper,
    MainContainer,
    InputField,
    CardWrapper,
    SignImage,
    CardInnerContainer,
    NoSignWrapper,
    CardFace,
    CardTitle,
} from './mulcharCalculatorStyles';

// Types
import { SignObj } from '../../../../articlePages/calendar/mulcharPage/types/componentTypes';

const MulcharCalculator: React.FC<{ signs: SignObj[] }> = ({ signs }) => {
    const { lang } = useGlobalData();
    const [year, setYear] = useState<string>('');
    const [index, setIndex] = useState<number | null>(null);

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        const inputYear = e.target.value;
        if (!/^\d{4}$/.test(inputYear)) {
            setYear(inputYear);
            setIndex(null);
            return;
        }
        const yearIndex = (parseInt(inputYear) - 16) % 12;
        setYear(inputYear);
        setIndex(yearIndex);
    };

    return (
        <MainContainer>
            <InputWrapper>
                <InputField
                    name="muljar_year"
                    id="muljar_year"
                    value={year}
                    onChange={handleChange}
                    placeholder={staticData.MULCHAR_CALC_PLACEHOLDER[lang]}
                />
            </InputWrapper>
            <CardWrapper>
                <CardInnerContainer
                    $flip={
                        index !== null &&
                        index >= 0 &&
                        index < signs.length &&
                        year.length > 0
                    }
                >
                    <CardFace $back={true}>
                        {index !== null &&
                        index >= 0 &&
                        index < signs.length &&
                        year.length > 0 ? (
                            <React.Fragment>
                                <CardTitle>{signs[index].info.title}</CardTitle>
                                <SignImage
                                    src={`${process.env.REACT_APP_BASE_URL}${staticData.MULCHAR_CHART_IMGS[index]?.img?.src}`}
                                    alt={signs[index]?.id}
                                />
                            </React.Fragment>
                        ) : null}
                    </CardFace>
                    <CardFace $back={false}>
                        <NoSignWrapper>?</NoSignWrapper>
                    </CardFace>
                </CardInnerContainer>
            </CardWrapper>
        </MainContainer>
    );
};

export default MulcharCalculator;
